import {TodoListST} from "./todo-list-types";
import {todoId1, todoId2, todoId3} from "../todo/todo-reducer";
import {ActionsType} from "../../store/store";

const initialState: TodoListST = {
  todoList: [
    {
      id: todoId1,
      filter: 'all',
      heading: 'What to eat?'
    },
    {
      id: todoId2,
      filter: 'all',
      heading: 'What to buy?'
    },
    {
      id: todoId3,
      filter: 'all',
      heading: 'What to do?'
    },
  ]
}

export const todoListReducer = (state = initialState, action: ActionsType): TodoListST => {
  switch (action.type) {
    case "todo-list/CREATE-TODO":
      return {
        ...state,
        todoList: [...state.todoList, action.payload]
      }
    case 'todo/DELETE-TODO':
      const newTodoList = state.todoList.filter(task => task.id !== action.payload.id)
      return {
        ...state,
        todoList: newTodoList
      }
    case 'todo/FILTER-TASK':
      return {
        ...state,
        todoList: state.todoList.map((todo) => todo.id === action.payload.id
          ? {
            ...todo,
            filter: action.payload.filter
          }
          : todo)
      }
  }
  return state
}


