import React, {ChangeEvent, FC, useState, KeyboardEvent} from 'react'

import {
  Box,
  Button,
  ButtonGroup, Checkbox,
  IconButton, List, ListItem,
  Paper,
  TextField,
  Typography
} from '@mui/material'
import {Delete} from '@mui/icons-material'
import AddIcon from '@mui/icons-material/Add';

import {TodoPropsType} from "./todo-types";
import {useDispatch, useSelector} from "react-redux";
import {RootST} from "../../store/store";
import {addTaskAC, changeTaskStatusAC, deleteTaskAC} from "./todo-actions";

import style from './todo.module.css'
import {deleteTodoAC, filterTaskAC} from "../todo-list/todo-list-actions";
import {EditableTitle} from "../../components/editable-title";

export const TodoComponent: FC<TodoPropsType> = ({
   heading, id, filter
 }) => {
  const dispatch = useDispatch()
  const tasks = useSelector((state: RootST) => state.tasksList.tasks[id])

  let filteredTasks = tasks
  if(filter === 'active') {
    filteredTasks = tasks.filter((task) => !task.isDone )
  } else if (filter === 'completed') {
    filteredTasks = tasks.filter((task)=> task.isDone)
  }

  const tasksListArray = filteredTasks.map(task => {
    const handleChangeStatusTask = (event: ChangeEvent<HTMLInputElement>) => {
      dispatch(changeTaskStatusAC(id, task.id, event.currentTarget.checked))
    }

    const handleDeleteTask = () => {
      dispatch(deleteTaskAC(id, task.id))
    }

    return (
      <ListItem
        key={task.id}
        secondaryAction={
          <IconButton edge="end" aria-label="comments" color={'error'} onClick={handleDeleteTask}>
            <Delete  />
          </IconButton>
                        }
        disablePadding>
        <Checkbox checked={task.isDone} onChange={handleChangeStatusTask}/>
        {task.title}
      </ListItem>
    );
  })

  const handleDeleteTodo =() => {
    dispatch(deleteTodoAC(id))
  }

  const [title, setTitle] = useState('')
  const handleChangeInputValue = (event: ChangeEvent<HTMLInputElement>) => {
    setTitle(event.currentTarget.value)
  }

  const handleAddTask = () => {
    dispatch(addTaskAC(id, title))
  }

  const handleAllButton = () => {
    dispatch(filterTaskAC(id, "all"))
  }

  const handleActiveButton = () => {
    dispatch(filterTaskAC(id, "active"))
  }

  const handleCompletedButton = () => {
    dispatch(filterTaskAC(id, "completed"))
  }

  const handleEnter = (event: KeyboardEvent<HTMLInputElement>) => {
    event.key === 'Enter' && handleAddTask()
  }

  const changeHeading = (heading: string) => {
    // dispatch()
  }

  return (
    <Box key={id}>
      <Paper elevation={3} sx={{p: 2}}>
        <Typography variant='h4' component='h3'>
          <EditableTitle title={heading} changeTitle={changeHeading}/>
          <IconButton aria-label='delete' onClick={handleDeleteTodo} color={'error'} >
            <Delete/>
          </IconButton>
        </Typography>
        <Box className={style.WrapInput}>
          <TextField id="outlined-basic" label="Name task" variant="outlined" onKeyDown={handleEnter} value={title} onChange={handleChangeInputValue}/>
          <IconButton className={style.IconButton} onClick={handleAddTask}>
            <AddIcon/>
          </IconButton>
        </Box>
        <List>
          {tasksListArray}
        </List>
        <ButtonGroup
          variant='contained'
          aria-label='outlined primary button group'
        >
          <Button onClick={handleAllButton} color={filter === 'all' ? 'success' : 'primary'}>
            all
          </Button>
          <Button onClick={handleActiveButton} color={filter === 'active' ? 'success' : 'primary'}>
            active
          </Button>
          <Button onClick={handleCompletedButton} color={filter === 'completed' ? 'success' : 'primary'}>
            completed
          </Button>
        </ButtonGroup>
      </Paper>
    </Box>
  )
}
