import {v4} from "uuid";

export const changeTaskStatusAC = (id: string, taskId: string, isDone: boolean, ) => ({
  type: 'todo/CHANGE-TASK-STATUS' as const,
  payload: {
    id,
    taskId,
    isDone
  }
})

export const deleteTaskAC = (id: string, taskId: string) => ({
  type: 'todo/DELETE-TASK' as const,
  payload: {
    id,
    taskId,
  }
})

export const addTaskAC = (id: string, text: string) => ({
  type: 'todo/CREATE-TASK' as const,
  payload: {
    taskId: v4(),
    id,
    text,
  }
})
