import React, {ChangeEvent, KeyboardEvent, useState} from 'react';
import {Box, Button, TextField} from "@mui/material";

import {Todo} from "../todo";
import {useDispatch, useSelector} from "react-redux";
import {RootST} from "../../store/store";

import style from './todo-list.module.css'
import {createTodoAC} from "./todo-list-actions";

export const TodoListComponent = () => {

  const dispatch = useDispatch()
  const todoList = useSelector((state: RootST) => state.todoList.todoList)

  const todosArray = todoList.map(todo => {
    return (
      <Box key={todo.id}>
        <Todo
          id={todo.id}
          heading={todo.heading}
          filter={todo.filter}
        />
      </Box>
    )
  })


  const [title, setTitle] = useState('')
  const handleCreateTodo = () => {
    const action = createTodoAC(title);
    dispatch(action)
    setTitle('')
  };

  const handleChangeTodoTitle = (event: ChangeEvent<HTMLInputElement>) => {
    setTitle(event.currentTarget.value)
  };

  const handleCreateEnter = (event: KeyboardEvent<HTMLInputElement>) => {
    event.key === 'Enter' && handleCreateTodo()
  }

  return (
    <Box>
      <Box className={style.CreateTodo}>
        <TextField
          onKeyDown={handleCreateEnter}
          onChange={handleChangeTodoTitle}
          value={title}
          id="standard-basic"
          label="New todo"
          variant="standard"/>
        <Button
          onClick={handleCreateTodo}
          variant={'contained'}
          className={style.ButtonCreate}
        >
          Create
        </Button>
      </Box>
      <Box className={style.Wrap}>
        {
          todosArray.length
            ? todosArray
            : <Box>Add first todo list</Box>
        }
      </Box>
    </Box>
  );
}