import {v4} from "uuid";
import {FilterType} from "../../core/types";

export const createTodoAC = (title: string) => ({
  type: 'todo-list/CREATE-TODO' as const,
  payload: {
    id: v4(),
    filter: 'all' as FilterType,
    heading: title
  }
})

export const filterTaskAC = (id: string, filter: FilterType) => ({
  type: 'todo/FILTER-TASK' as const,
  payload: {
    id,
    filter
  }
})

export const deleteTodoAC = (id: string) => ({
  type: 'todo/DELETE-TODO' as const,
  payload: {
    id
  }
})