import {v4} from 'uuid'

import {TasksTypeST} from "./todo-types";
import {ActionsType} from "../../store/store";

export const todoId1 = v4()
export const todoId2 = v4()
export const todoId3 = v4()

const initialStateTasks: TasksTypeST = {
  tasks: {
    [todoId1]: [
      {id: v4(), title: 'Bread', isDone: false},
      {id: v4(), title: 'Milk', isDone: true},
      {id: v4(), title: 'Sugar', isDone: true},
      {id: v4(), title: 'Salt', isDone: false},
      {id: v4(), title: 'Eggs', isDone: true}
    ],
    [todoId2]: [
      {id: v4(), title: 'Learn CRUD', isDone: false},
      {id: v4(), title: 'Repeat create', isDone: false}
    ],
    [todoId3]: [
      {id: v4(), title: 'Bread', isDone: false},
      {id: v4(), title: 'Milk', isDone: true}
    ]
  }

}

export const tasksReducer = (state = initialStateTasks, action: ActionsType): TasksTypeST => {
  switch (action.type) {
    case 'todo-list/CREATE-TODO':
      return {
        ...state,
        tasks: {
          ...state.tasks,
          [action.payload.id]: []
        }
      }
    case 'todo/DELETE-TODO':
      // const newState: TasksType = {}
      // for (const id in state.tasks) {
      //   if (id !== action.payload.id) {
      //     newState[id] = state.tasks[id]
      //   }
      // }
      // return {
      //   ...state,
      //   tasks: newState
      // }

      // const newTasksArray = Object.entries(state.tasks)
      //   .filter((entry) => entry[0] !== action.payload.id)
      // const newTasks = Object.fromEntries(newTasksArray)
      // return {
      //   ...state,
      //   tasks: newTasks
      // }

      const newState = {
        ...state,
        tasks: {
          ...state.tasks
        }
      }
      delete newState.tasks[action.payload.id]
      return newState
    case 'todo/CHANGE-TASK-STATUS':
      const todoId = action.payload.id
      return {
        ...state,
        tasks: {
          ...state.tasks,
          [todoId]: state.tasks[todoId].map((task) =>
            task.id === action.payload.taskId
              ? {
                ...task,
                isDone: action.payload.isDone
              }
              : task)
        }
      }
    case 'todo/DELETE-TASK': //проговорить на занятии логику в СЛУУУУУХ :)
      return {
        ...state,
        tasks: {
          ...state.tasks,
          [action.payload.id]: state.tasks[action.payload.id].filter(task => task.id !== action.payload.taskId)
        }
        // [action.payload.id]: state.tasks[action.payload.id ].filter((task)=> task.id !== action.payload.taskId)
      }
    case 'todo/CREATE-TASK': //проговорить на занятии логику в СЛУУУУУХ :)
      const newTask = {id: action.payload.taskId, title: action.payload.text, isDone: false}
      const newTasks = [newTask, ...state.tasks[action.payload.id]]
      return {
        ...state,
        tasks: {
          ...state.tasks,
          [action.payload.id]: newTasks
        }
      }

  }

  return state
}
