import React, {ChangeEvent, FC, useState, KeyboardEvent} from 'react';
import {EditableTitlePropsTypes} from "./editable-title-types";

export const EditableTitleComponent: FC<EditableTitlePropsTypes> = ({title, changeTitle}) => {

  const [value, setValue] = useState(title)
  const [isEdit, setEdit] = useState(false)

  const handleOnDoubleClick = () =>{
    setEdit(true)
  }

  const handleOnChange = (event: ChangeEvent<HTMLInputElement>) => {
    setValue(event.currentTarget.value)
  }

  // потеря фокуса
  const handleOnBlur = () => {
    setEdit(false)
    changeTitle(value)
  }

  const handleOnEnter = (event: KeyboardEvent<HTMLInputElement>) => {
    event.key === 'Enter' && handleOnBlur()
  }

  return (
    <>
      {
        isEdit
          ? <input value={value} onChange={handleOnChange} onBlur={handleOnBlur} onKeyDown={handleOnEnter}/>
          : <span onDoubleClick={handleOnDoubleClick}>{value}</span>
      }
    </>
  );
}