import {combineReducers, legacy_createStore as createStore} from 'redux'
import {todoListReducer} from "../pages/todo-list/todo-list-reducer";
import {tasksReducer} from "../pages/todo/todo-reducer";
import {TasksListAT} from "../pages/todo/todo-types";
import {TodoListAT} from "../pages/todo-list/todo-list-types";

const rootReducer = combineReducers({
  todoList: todoListReducer,
  tasksList: tasksReducer
})

export const store = createStore(rootReducer)

export type ActionsType =
  | TodoListAT
  | TasksListAT

export type RootST = ReturnType<typeof rootReducer>

// @ts-ignore
window.state = store.getState()


